


















import Wecropper from '../../components/we-cropper.vue';
import WeCropper from '../../utils/js/we-cropper.js';
const device = uni.getSystemInfoSync();
const width = device.windowWidth;
const height = device.windowHeight - 50;
export default {
	data() {
		return {
			filePath: '', //裁剪后图片路径
			cropperOpt: {
				id: 'cropper',
				width,
				height,
				scale: 2.5,
				zoom: 8,
				cut: {
					x: (width - 300) / 2,
					y: (height - 300) / 2,
					width: 300,
					height: 300
				}
			}
		}
	},
	methods: {
		touchStart(e) {
			this.wecropper.touchStart(e);
		},
		touchMove(e) {
			this.wecropper.touchMove(e);
		},
		touchEnd(e) {
			this.wecropper.touchEnd(e);
		},
		getCropperImage() {
			this.wecropper.getCropperImage((avatar) => {
				if (avatar) {
					this.filePath = avatar;
					console.log(avater);
				}
			})
		},
		uploadTap() {
			let that = this;
			wx.chooseImage({
				count: 1, // 默认9
				sizeType: ['compressed'],
				success(res) {
					const src = res.tempFilePaths[0];
					//  获取裁剪图片资源后，给data添加src属性及其值
					that.wecropper.pushOrign(src);
				}
			})
		}
	},
	onLoad(option) {
		console.log('src: ' + option.src);
		if (option.src) {
			this.cropperOpt.src = option.src
			new WeCropper(this.cropperOpt)
				.on('ready', (ctx) => {
					console.log(`wecropper is ready for work!`)
				})
				.on('beforeImageLoad', (ctx) => {
					console.log(`before picture loaded, i can do something`)
					console.log(`current canvas context:`, ctx)
					uni.showToast({
						title: '上传中',
						icon: 'loading',
						duration: 20000
					})
				})
				.on('imageLoad', (ctx) => {
					console.log('picture loaded')
					console.log('current canvas context:', ctx)
					uni.hideToast()
				})
				.on('beforeDraw', (ctx, instance) => {
					console.log(`before canvas draw,i can do something`)
					console.log(`current canvas context:`, ctx)
				})
				.updateCanvas()
		}
	},
	components:{
		Wecropper
	}
}



